.contact-me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-me-header {
  border-bottom: 5px solid orange;
}

.contact-me-text {
  width: 40%;
}

.contact-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  height: 100px;
  width: 20%;
}

@media screen and (max-width: 1000px) {
  .contact-me-text {
    width: 80%;
  }

  .contact-icons {
    justify-content: space-between;
  }
}